<template>
  <div class="inspectionreportprint">
    <div>
      <v-card-title></v-card-title>

      <v-card-text class="mt-3">
        <!-- 畫面顯示內容區域 -->
        <h3>{{ cData.company_name }}</h3>

        <div class="text-center border-bottom my-2">
          <h1>
            <strong>檢驗報告單</strong>
          </h1>
        </div>

        <table border="3px #000000 solid;" width="100%">
          <thead class="form_title">
            <tr>
              <th class="p-2">
                <v-row>
                  <v-col cols="2">病歷號: {{ uData.p_code }}</v-col>
                  <v-col cols="2">姓名: {{ uData.name }}</v-col>
                  <v-col cols="2">床號: {{ uData.bed_name}}</v-col>
                  <v-col cols="2">性別: {{ uData.sex }}</v-col>
                  <v-col cols="2">生日: {{ uData.birthday }}</v-col>
                  <v-col cols="2">年齡: {{ age }}</v-col>
                </v-row>
              </th>
            </tr>
          </thead>

          <tbody v-for="list in items" :key="list.snkey">
            <tr>
              <td class="grey lighten-2 px-2">
                <v-row>
                  <v-col cols="3">評估日期: {{ list.date }}</v-col>
                  <v-col cols="3">檢驗地點: {{ list.location }}</v-col>
                  <v-col cols="3"
                    >評估人: {{ list.create_man | filterName }}<strong class="ml-3">簽名:</strong></v-col
                  >
                </v-row>
              </td>
            </tr>

            <tr>
              <td>
                <v-row class="px-2">
                  <v-col cols="2">白血球: {{ list.leukocyte }}</v-col>
                  <v-col cols="2">紅血球: {{ list.erythrocyte }}</v-col>
                  <v-col cols="2">血色素: {{ list.hemoglobin }}</v-col>
                  <v-col cols="2">血小板: {{ list.platelet }}</v-col>
                  <v-col cols="2">白蛋白: {{ list.albumin }}</v-col>
                  <v-col cols="2"
                    >醣化血色素: {{ list.glycosylatedhemoglobin }}</v-col
                  >
                  <v-col cols="2">GOT(AST): {{ list.got }}</v-col>
                  <v-col cols="2">GPT(ALT): {{ list.gpt }}</v-col>
                  <v-col cols="2">血糖: {{ list.bloodsugar }}</v-col>
                  <v-col cols="2">膽固醇: {{ list.cholesterol }}</v-col>
                  <v-col cols="2">TG: {{ list.tg }}</v-col>
                  <v-col cols="2">BUN: {{ list.bun }}</v-col>
                  <v-col cols="2">CRE: {{ list.cre }}</v-col>
                  <v-col cols="2">UA: {{ list.ua }}</v-col>
                  <v-col cols="2">Na: {{ list.na }}</v-col>
                  <v-col cols="2">K: {{ list.k }}</v-col>
                  <v-col cols="2">HDL/LDL: {{ list.hdlldl }}</v-col>
                </v-row>
              </td>
            </tr>
            <tr>
              <th class="px-2">尿液</th>
            </tr>
            <tr>
              <td>
                <v-row class="px-2">
                  <v-col cols="2">PH: {{ list.ph }} </v-col>
                  <v-col cols="2">尿蛋白: {{ list.urineprotein }} </v-col>
                  <v-col cols="2">OB: {{ list.ob }} </v-col>
                  <v-col cols="2">RBC: {{ list.rbc }} </v-col>
                  <v-col cols="2">WBC: {{ list.wbc }} </v-col>
                  <v-col cols="2">Bacteria: {{ list.bacteria }} </v-col>
                  <v-col cols="2">GLU: {{ list.glu }} </v-col>
                </v-row>
              </td>
            </tr>
            <tr>
              <th class="px-2">細菌培養</th>
            </tr>
            <tr>
              <td>
                <v-row class="px-2">
                  <v-col cols="2">Urine: {{ list.urine }} </v-col>
                  <v-col cols="2">Sputum: {{ list.sputum }} </v-col>
                  <v-col cols="12">其他備註: {{ list.note }} </v-col>

                </v-row>
              </td>
            </tr>
            <tr>
              <td class="px-2">處置: {{ list.dispose }}</td>
            </tr>
            <tr>
              <td class="px-2">追蹤: {{ list.track }}<strong class="ml-3">簽名:</strong></td>
            </tr>
          </tbody>
        </table>
      </v-card-text>

      <v-divider></v-divider>

      <v-card-actions></v-card-actions>
      <!-- <pre>{{items}}</pre> -->
    </div>
  </div>
</template>

<script>
import qs from "qs";
import dayjs from "dayjs";

export default {
  data() {
    return {
      cData: {},
      pData: {},
      uData: {},
      list: {}, //表單內資料
      items: [],
      // nursingassessmentItems: {},
      // nutritionItems: [],
      // recordItems: []
    };
  },

  filters: {
    filterName(val) {
      if (val != undefined) {
        var name = val.split("(");
        return name[0];
      }
    },
  },

  computed: {
    age() {
      return dayjs().format("YYYY") - dayjs(this.uData.birthday).format("YYYY");
    },
  },

  mounted() {
    this.cData = JSON.parse(sessionStorage.getItem("cData"));
    this.pData = JSON.parse(sessionStorage.getItem("pData"));
    this.uData = JSON.parse(sessionStorage.getItem("uData"));
    // this.list = JSON.parse(sessionStorage.getItem("printData"));
    this.items = JSON.parse(sessionStorage.getItem("printData"));
  },

  methods: {},
};
</script>

<style lang="scss" scoped>
@import "node_modules/bootstrap/scss/bootstrap";
@import "node_modules/bootstrap-vue/src/index.scss";
.inspectionreportprint {
  font-size: 1rem;

  .form_title {
    font-size: 1.2rem;
    font-family: "微軟正黑體";
  }

  .col{
    padding-top:0;
    padding-bottom:0;
  }  


}

@media print {
  .inspectionreportprint {
    font-size: 0.6rem;

    .form_title {
      font-size: 1rem;
      font-family: "微軟正黑體";
    }
  }
}
</style>